@import '../../vendor/variables.scss';
.dialog {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	background: rgba(0, 0, 0, 0.8);
	transition: opacity 250ms;
	&__body {
		position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 340px;
    padding: 20px;
		border-radius: 12px;
    background: $main;
    box-shadow: 0 0 50px rgb(0 0 0 / 50%);
    transform: translateX(100%);
    text-align: center;
	}
	&__content {
		input {
			opacity: 0;
			text-align: center;
			margin: 0 auto;
			z-index: 99;
			position: relative;
			cursor: pointer;
		}
	}
	&__file {
		color: $blue;
    font-weight: bolder;
    cursor: pointer;
    font-size: 12px;
    position: relative;
		top: -23px;
	}
}
